<template>
  <div class="all">
      <header>
        <ul>
          <li><span style="margin:0 10px 15px -10px">IP:</span><span><el-input v-model="searchValue.ip"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">境内外:</span><span>
              <el-select v-model="value2"  clearable placeholder="请选择" ref="selectLable2">
                <el-option
                  v-for="item in options2"
                  :key="item.value2"
                  :label="item.label2"
                  :value="item.value2">
                </el-option>
              </el-select>
            </span></li>
          <li><span style="margin:0 10px 15px 12px">起始时间:</span><span><el-input v-model="searchValue.s_time"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">终止时间:</span><span><el-input v-model="searchValue.e_time"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">最新时间:</span><span><el-input v-model="searchValue.time"></el-input></span></li>
          <li><span style="margin:0 10px 15px -12px">备案归属:</span><span><el-input v-model="searchValue.owner"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">数据库类型:</span>
            <span>
              <el-select v-model="value"  clearable placeholder="请选择" ref="selectLable1">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </span></li>
          <li><span style="margin:0 10px 10px 12px">省:</span><span><el-input v-model="searchValue.province"></el-input></span></li>
          <li><span style="margin:0 10px 10px 12px">市:</span><span><el-input v-model="searchValue.city"></el-input></span></li>
          <li><span style="margin:0 10px 10px 12px">区:</span><span><el-input v-model="searchValue.district"></el-input></span></li>
          <li><span><el-button @click="search()">查询</el-button></span></li>
        </ul>
      </header>
      <section class="Body">
        <el-table
          :data="tableData"
          border
          style="width:95%;margin: auto;"
          >
          <el-table-column
            fixed
            prop="dip"
            label="IP"
            min-width="125"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="port"
            label="端口"
            min-width="120"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="min_time"
            label="起始时间"
            min-width="120"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="max_time"
            label="终止时间"
            min-width="120"
            show-overflow-tooltip>
          </el-table-column>
          
          <el-table-column
            prop="s_c_boundary"
            label="备案归属"
            min-width="120"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="database_type"
            label="数据库类型"
            min-width="100"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="c_region"
            label="省"
            min-width="100"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="c_city"
            label="市"
            min-width="100"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="c_district"
            label="区"
            min-width="110"
            show-overflow-tooltip>

          </el-table-column>
          <el-table-column
            prop="company"
            label="单位"
            min-width="110"
            show-overflow-tooltip>

          </el-table-column>
        </el-table>
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableData.length"
              >
              </el-pagination>
      </section>
  </div>
</template>

<script>
  import axios from 'axios';
  import { Message } from "element-ui";
  export default {
  name:'info1',
  data(){
    return{
      currentPage: 1,
      pagesize: 10,
      searchValue:{
        ip: "",
        time:"",
        s_time: "",
        d_time: "",
        owner: "",
        province: "",
        city: "",
        district: ""

      },
      tableData:[{

      }],
      options: [{
          value: '选项1',
          label: 'Mysql'
        }, {
          value: '选项2',
          label: 'MongoDB'
        }, {
          value: '选项3',
          label: 'Redis'
        }, {
          value: '选项4',
          label: 'ElasticSearch'
        }, {
          value: '选项5',
          label: 'PostgreSQL'
        }],
        value: '',
        options2: [{
          value2: '选项1',
          label2: '境内'
        }, {
          value2: '选项2',
          label2: '境外'
        }],
        value2: ''
    }
    
  },
  methods:{
    handleSizeChange(val) {
      this.pagesize = val;
      //console.log(`每页${val}条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      //console.log(`当前页: ${val}`);
    },
    search(){
      // this.loading = true;
      axios({
        method:"post",
        url:"/search_database_info",
        data: {
            token: this.$store.state.token,
            req_data: {
              ip: this.searchValue.ip,
              area_type: this.$refs.selectLable2.selected.label,
              time: this.searchValue.time,
              s_time: this.searchValue.s_time,
              d_time: this.searchValue.d_time,
              owner: this.searchValue.owner,
              database_type:this.$refs.selectLable1.selected.label,
              province: this.searchValue.province,
              city: this.searchValue.city,
              district: this.searchValue.district
            },
        },
      })
        .then((rep) => {
          this.tableData = rep.data.data;
          // this.loading = false;
        }).catch(()=> {
          Message({
            message: "用户信息异常,请重试！",
            type: "error",
          });
           window.sessionStorage.clear();
            // this.$store.dispatch("updateuserinfo", []);
            this.$router.replace("/");
            //触发后禁止浏览器的后退键
            history.pushState(null, null, document.URL);
            window.addEventListener(
              "popstate",
              function (e) {
                history.pushState(null, null, document.URL);
              },
              false
            );
        });
        
    },
  },
  mounted(){
    this.search()
  }
}
</script>

<style scoped lang="less">
.all{
  border:1px solid #dcdfe6;
}
li{
    display: inline-block;
    position: relative;
    list-style-type: none;
  }
  /deep/.el-input__suffix {
  top: -1px;
}
/deep/.el-input__icon {
  line-height: inherit;
}
/deep/  .el-input__inner {
    background-color: #FFF;
    background-image: none;
    border-radius: 4px;
    border: 1px solid #DCDFE6;
    box-sizing: border-box;
    color: #606266;
    display: inline-block;
    font-size: inherit;
    height: 20px;
    line-height: 20px;
    outline: 20px;
    padding: 0 8px;
    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    width: 125px;
}
/deep/.el-button {
  line-height: 0.1;
  border-radius: 5px;
  letter-spacing: 10px;
  padding:12px 21px 12px 28px;
  margin:8px 0 5px -12px;
}
li span{
  display: inline-block;
  // float: right;
}
.Body{
  margin: 0 auto;
  text-align: center;
}

</style>
