<template>
  <div class="all">
      <header>
        <ul>
          <li><span style="margin:0 10px 15px -10px">源IP:</span><span><el-input v-model="searchValue.sourceIP"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">目的IP:</span><span><el-input v-model="searchValue.destIP"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">时间:</span><span><el-input v-model="searchValue.time"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">备案归属:</span><span><el-input v-model="searchValue.record"></el-input></span></li>
          <li><span style="margin:0 10px 15px 12px">数据库类型:</span>
            <span>
              <el-select v-model="value" clearable placeholder="请选择" ref="selectLable1">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </span></li>
          <li><span style="margin:0 10px 10px -11px">目的IP省:</span><span><el-input v-model="searchValue.destProvince"></el-input></span></li>
          <li><span style="margin:0 10px 10px 12px">目的IP市:</span><span><el-input v-model="searchValue.destCity"></el-input></span></li>
          <li><span style="margin:0 10px 10px 12px">目的IP区:</span><span><el-input v-model="searchValue.destDistrict"></el-input></span></li>
          <li><span><el-button @click="search()">查询</el-button></span></li>
        </ul>
      </header>
      <section class="Body">
        <el-table
          :data="tableData"
          border
          style="width:95%;margin: auto;"
          >
          <el-table-column
            fixed
            prop="event_time"
            label="时间"
            min-width="110"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="database_type"
            label="数据库类型"
            min-width="120"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="status"
            label="操作类型"
            min-width="120"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="sip"
            label="源IP"
            min-width="125"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="sport"
            label="源端口"
            min-width="80"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="dip"
            label="目的IP"
            min-width="120"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="dport"
            label="目的端口"
            min-width="80"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="d_region"
            label="省份"
            min-width="100"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="d_city"
            label="地市"
            min-width="110"
            show-overflow-tooltip>

          </el-table-column>
          <el-table-column
            prop="d_district"
            label="区县"
            min-width="110"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="d_owner"
            label="IP备案信息"
            min-width="110"
            show-overflow-tooltip>
          </el-table-column>
        </el-table>
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableData.length"
              >
              </el-pagination>
      </section>
  </div>
</template>

<script>
  import axios from 'axios';
  import { Message } from "element-ui";
  export default {
  name:'info1',
  data(){
    return{
      currentPage: 1,
      pagesize: 10,
      searchValue:{
        sourceIP:"",
        destIP:"",
        time:"",
        record:"",
        destProvince:"",
        destCity:"",
        destDistrict:"",

      },
      tableData:[{
        // event_time:"2022-09-04",
        // database_type:"Elasticsearch",
        // status:"es登陆成功",
        // sip:"18.183.247.190",
        // sport:50068,
        // dip:"222.212.90.33",
        // dport:9200,
        // d_region:"四川省",
        // d_city:"成都市",
        // d_district:"市辖区",
        // d_owner:"锦江肖小儿中医门诊部"
      }],
      options: [{
          value: '选项1',
          label: 'Mysql'
        }, {
          value: '选项2',
          label: 'MongoDB'
        }, {
          value: '选项3',
          label: 'Redis'
        }, {
          value: '选项4',
          label: 'ElasticSearch'
        }, {
          value: '选项5',
          label: 'PostgreSQL'
        }],
        value: ''
    }
    
  },
  methods:{
    handleSizeChange(val) {
      this.pagesize = val;
      //console.log(`每页${val}条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      //console.log(`当前页: ${val}`);
    },
    search(){
      // this.loading = true;
      axios({
        method:"post",
        url:"/search_operate_history",
        data: {
            token: this.$store.state.token,
            req_data: {
              sip: this.searchValue.sourceIP,
              dip: this.searchValue.destIP,
              time: this.searchValue.time,
              owner: this.searchValue.record,
              database_type:this.$refs.selectLable1.selected.label,
              d_province: this.searchValue.destProvince,
              d_city: this.searchValue.destCity,
              d_district: this.searchValue.destDistrict
            },
        },
      })
        .then((rep) => {
          console.log("执行了then")
          console.log(rep.data.data)
          this.tableData = rep.data.data;
          // this.loading = false;
        }).catch(()=> {
          console.log("执行了catch")
          Message({
            message: "用户信息异常,请重试！",
            type: "error",
          });
            window.sessionStorage.clear();
            // this.$store.dispatch("updateuserinfo", []);
            this.$router.replace("/");
            //触发后禁止浏览器的后退键
            history.pushState(null, null, document.URL);
            window.addEventListener(
              "popstate",
              function (e) {
                history.pushState(null, null, document.URL);
              },
              false
            );
        });
        ;
    },
  },
  mounted(){
    this.search()
  }
}
</script>

<style scoped lang="less">
.all{
  border:1px solid #dcdfe6;
}
li{
    display: inline-block;
    // float: left;
    position: relative;
    list-style-type: none;
  }
  /deep/.el-input__suffix {
  top: -1px;
}
/deep/.el-input__icon {
  line-height: inherit;
}
/deep/  .el-input__inner {
    background-color: #FFF;
    background-image: none;
    border-radius: 4px;
    border: 1px solid #DCDFE6;
    box-sizing: border-box;
    color: #606266;
    display: inline-block;
    font-size: inherit;
    height: 20px;
    line-height: 20px;
    outline: 20px;
    padding: 0 8px;
    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    width: 125px;
}
/deep/.el-button {
  line-height: 0.1;
  border-radius: 5px;
  letter-spacing: 10px;
  padding:12px 21px 12px 28px;
  margin:8px 0 5px 38px;
}
li span{
  display: inline-block;
  // float: right;
}
.Body{
  margin: 0 auto;
  text-align: center;
}

</style>
